import Axios from '@/axios'

const state = {
    authUser: undefined
};

const getters = {
    authUser: state => state.authUser
}

const mutations = {
    SET_AUTH_USER: (state, authUser) => {
        state.authUser = authUser
    },
}

const actions = {
    getAuthUser: async ({ commit }) => {
        
        const authUser  = (await Axios.get('users/auth')).data

        if(authUser){
            commit('SET_AUTH_USER', authUser)
        }

        return authUser

    }
}

export default {
    state,
    getters,
    mutations,
    actions,
};