<template>
  <div class="shadow-sm p-4 rounded bg-light" style="margin-top:60px;">
    <h1 class="mb-3">Добро пожаловать</h1>
    <div v-if="authUser">
      Вы вошли как {{authUser.login}} [роль: {{authUser.role}}, тарифный план: {{authUser.tariff}}, id: {{authUser.id}}] <br><br>
      <button type="button" class="btn btn-outline-primary" @click="logout()">Выйти</button>
    </div>
    <div v-else>
      <router-link class="btn btn-primary" to="/signin">Войти</router-link>
      <router-link class="btn btn-primary ml-2" to="/signup">Создать аккаунт</router-link>
    </div>

    <div class="mt-3" v-if="!authUser">
      <strong>Readme:</strong>
      На данный момент система абсолютно минималистична и в стадии самого начала разработки
      Для входа как админ используйте следующую комбинацию логина и пароля: admin admin
    </div>

  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  computed:{
    ...mapGetters(['authUser']),
  },
  methods:{
    logout(){
      localStorage.removeItem('jwtToken')
      this.$router.go()
    }
  }
}
</script>
