import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home.vue'
import store from '@/store'

const routes = [
  {
    path: '/',
    name: 'Главная',
    component: Home,
    meta: { }
  },
  {
    path: '/createuser',
    name: 'Создать пользователя',
    component: () => import('../views/CreateUser.vue'),
    meta: { requiresAuth: true, requiresAdmin: true }
  },
  {
    path: '/ATS/Create',
    name: 'Добавить ATS',
    component: () => import('../views/listPages/ATS/Create.vue'),
    meta: { requiresAuth: true, requiresAdmin: true }
  },
  {
    path: '/ATS/List',
    name: 'Список ATS',
    component: () => import('../views/listPages/ATS/List.vue'),
    meta: { requiresAuth: true, requiresAdmin: true }
  },
  {
    path: '/Hodovye_kachestva/Create',
    name: 'Добавить ходовое качество',
    component: () => import('../views/listPages/Hodovye_kachestva/Create.vue'),
    meta: { requiresAuth: true, requiresAdmin: true }
  },
  {
    path: '/Hodovye_kachestva/List',
    name: 'Список ходовых качеств',
    component: () => import('../views/listPages/Hodovye_kachestva/List.vue'),
    meta: { requiresAuth: true, requiresAdmin: true }
  },
  {
    path: '/Gabarity/Create',
    name: 'Добавить габариты',
    component: () => import('../views/listPages/Gabarity/Create.vue'),
    meta: { requiresAuth: true, requiresAdmin: true }
  },
  {
    path: '/Gabarity/List',
    name: 'Список габаритов',
    component: () => import('../views/listPages/Gabarity/List.vue'),
    meta: { requiresAuth: true, requiresAdmin: true }
  },
  {
    path: '/Harakteristiki_dvigatelya/Create',
    name: 'Добавить двигатель и характеристики',
    component: () => import('../views/listPages/Harakteristiki_dvigatelya/Create.vue'),
    meta: { requiresAuth: true, requiresAdmin: true }
  },
  {
    path: '/Harakteristiki_dvigatelya/List',
    name: 'Двигатели и характеристики',
    component: () => import('../views/listPages/Harakteristiki_dvigatelya/List.vue'),
    meta: { requiresAuth: true, requiresAdmin: true }
  },
  {
    path: '/Informaciya_o_tekhnicheskih_uzlah_i_agregatah/Create',
    name: 'Добавить информацию о технических узлах и агрегатах',
    component: () => import('../views/listPages/Informaciya_o_tekhnicheskih_uzlah_i_agregatah/Create.vue'),
    meta: { requiresAuth: true, requiresAdmin: true }
  },
  {
    path: '/Informaciya_o_tekhnicheskih_uzlah_i_agregatah/List',
    name: 'Показать информацию о технических узлах и агрегатах',
    component: () => import('../views/listPages/Informaciya_o_tekhnicheskih_uzlah_i_agregatah/List.vue'),
    meta: { requiresAuth: true, requiresAdmin: true }
  },
  {
    path: '/Massa/Create',
    name: 'Добавить массу',
    component: () => import('../views/listPages/Massa/Create.vue'),
    meta: { requiresAuth: true, requiresAdmin: true }
  },
  {
    path: '/Massa/List',
    name: 'Показать массу',
    component: () => import('../views/listPages/Massa/List.vue'),
    meta: { requiresAuth: true, requiresAdmin: true }
  },
  {
    path: '/Transmissiya_i_kolyosa/Create',
    name: 'Добавить трнасмассию и колеса',
    component: () => import('../views/listPages/Transmissiya_i_kolyosa/Create.vue'),
    meta: { requiresAuth: true, requiresAdmin: true }
  },
  {
    path: '/Transmissiya_i_kolyosa/List',
    name: 'Показать трансмиссию и колеса',
    component: () => import('../views/listPages/Transmissiya_i_kolyosa/List.vue'),
    meta: { requiresAuth: true, requiresAdmin: true }
  },
  {
    path: '/signin',
    name: 'Войти',
    component: () => import('../components/SignIn.vue'),
    meta: { requiresAuth: false }
  },
  {
    path: '/signup',
    name: 'Зарегистрироваться',
    component: () => import('../views/SignUp.vue'),
    meta: { requiresAuth: false}
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

router.beforeEach(async (to, from, next) => {
  console.log(to, from, next)

  const authUser = await store.dispatch('getAuthUser');

  if(to.meta.requiresAuth !== undefined){

    if(authUser && to.meta.requiresAuth === true){
      if(to.meta.requiresAdmin === true && authUser.role !== 'admin') return;
      next()
    }

    if(!authUser && to.meta.requiresAuth === false){
      next()
    }

  }else{
    next();
  }

  
})

export default router
